*{
    transition: all .3s linear;
}

.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 5px 10px 9px rgba(0,0,0, 0.25);
    padding: 1rem;
    margin: 1rem 0.5rem;
    border-radius: 20px;
    background-color: #83c9c2;
    
    position: relative;
}

.expense-item:hover {
    bottom: 2px;
    box-shadow: 8px 13px 12px rgba(0,0,0, 0.2);
    background-color: #2ec4b6;


}

.expense-item__date {
    font-size: 1rem;
    font-weight: 600;
    text-align: center;
    color:rgb(221, 212, 212);
}

.expense-item__description {
    display: flex;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    flex: 1;
}

.expense-item__description h2 {
    color: #fff;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
}

.expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #0c534d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 15px;
}

@media (min-width: 500px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }

    .expense-item__description h2 {
        font-size: 1.25rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem ;
    }
}