

.expense-date {
    display: flex;
    flex-direction: column;
    width: 6.5rem;
    height: 5rem;
    border: 1px solid #fff;
    background-color: rgb(92, 92, 92);
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}

.expense-date:hover {
    background-color:#0c534d;
}

.expense-date:hover .expense-date__year{
    color: #fff;
    letter-spacing: .3rem;
}

.expense-date__year {
    color: #0c534d;
    font-weight: 800;
    font-size: 1.2rem;
}

.expense-date__month {
    font-weight: 700;
    letter-spacing: .15rem;
    line-height: 1.4rem;
}

.expense-date__day {
    font-size: 1rem;
}